.toggler {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #fd5103;
  border-radius: 40px;
  transition-property: border, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.toggler:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 40px;
  transition-property: left, transform, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
}

.toggler.active {
  background-color: #fd5103;
  border: 1px solid #fff;
}

.toggler.active:after {
  background-color: #fff;
  left: 100%;
  transform: translateX(calc(-100% - 1px))translateY(-50%);
}

.toggler-mob {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #fd5103;
  border-radius: 40px;
  transition-property: border, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.toggler-mob:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 40px;
  transition-property: left, transform, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
}

.toggler-mob.active {
  background-color: #fd5103;
  border: 1px solid #fff;
}

.toggler-mob.active:after {
  background-color: #fff;
  left: 100%;
  transform: translateX(calc(-100% - 1px))translateY(-50%);
}

