}

.modal.is-hidden .modal__inner {
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: scale(.7);
}

.modal__close {
  z-index: 2;
