.loader-type1 {
  width: 40px;
  flex-grow: 0;
  flex-shrink: 0;
  display: inline-block;
  position: relative;
}

.loader-type1:after {
  content: "";
  padding-top: 100%;
  display: block;
}

.loader-type1.small {
  width: 20px;
  height: 20px;
}

.loader-type1-position1 {
  margin-right: -20px;
  top: 30px;
  right: -10px;
}

.loader-type1 div {
  width: 10%;
  height: 27%;
  transform-origin: 50% 185.185%;
  background: #fd5103;
  border-radius: 45%;
  animation: loader-type1 1.2s linear infinite;
  position: absolute;
  top: 0;
  left: 45%;
}

.loader-type1 div:nth-child(1) {
  animation-delay: -1.1s;
  transform: rotate(0deg);
}

.loader-type1 div:nth-child(2) {
  animation-delay: -1s;
  transform: rotate(30deg);
}

.loader-type1 div:nth-child(3) {
  animation-delay: -.9s;
  transform: rotate(60deg);
}

.loader-type1 div:nth-child(4) {
  animation-delay: -.8s;
  transform: rotate(90deg);
}

.loader-type1 div:nth-child(5) {
  animation-delay: -.7s;
  transform: rotate(120deg);
}

.loader-type1 div:nth-child(6) {
  animation-delay: -.6s;
  transform: rotate(150deg);
}

.loader-type1 div:nth-child(7) {
  animation-delay: -.5s;
  transform: rotate(180deg);
}

.loader-type1 div:nth-child(8) {
  animation-delay: -.4s;
  transform: rotate(210deg);
}

.loader-type1 div:nth-child(9) {
  animation-delay: -.3s;
  transform: rotate(240deg);
}

.loader-type1 div:nth-child(10) {
  animation-delay: -.2s;
  transform: rotate(270deg);
}

.loader-type1 div:nth-child(11) {
  animation-delay: -.1s;
  transform: rotate(300deg);
}

.loader-type1 div:nth-child(12) {
  animation-delay: 0s;
  transform: rotate(330deg);
}

@keyframes loader-type1 {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

